@font-face {
  font-family: BentonSansRegular;
  src: url("https://fonts.iu.edu/fonts/benton-sans-regular.eot");
  src: url("https://fonts.iu.edu/fonts/benton-sans-regular.eot?#iefix")
      format("embedded-opentype"),
    url("https://fonts.iu.edu/fonts/benton-sans-regular.woff") format("woff"),
    url("https://fonts.iu.edu/fonts/benton-sans-regular.ttf") format("truetype"),
    url("https://fonts.iu.edu/fonts/benton-sans-regular.svg#BentonSansRegular")
      format("svg");
  font-display: swap;
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: BentonSansBold;
  src: url("https://fonts.iu.edu/fonts/benton-sans-bold.eot");
  src: url("https://fonts.iu.edu/fonts/benton-sans-bold.eot?#iefix")
      format("embedded-opentype"),
    url("https://fonts.iu.edu/fonts/benton-sans-bold.woff") format("woff"),
    url("https://fonts.iu.edu/fonts/benton-sans-bold.ttf") format("truetype"),
    url("https://fonts.iu.edu/fonts/benton-sans-bold.svg#BentonSansBold")
      format("svg");
  font-display: swap;
  font-style: normal;
  font-weight: 400;
}
body > table.gssb_c {
  position: fixed !important;
}
#inline-search *,
#search *,
#toggles * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
#toggles,
div[id^="branding-bar"],
section[id^="branding-bar"] {
  -webkit-transition: all 0.42s ease-in-out;
  -moz-transition: all 0.42s ease-in-out;
  transition: all 0.42s ease-in-out;
}
.search-open #toggles,
.search-open div[id^="branding-bar"],
.search-open section[id^="branding-bar"] {
  margin-top: 50px;
}
#search,
#toggles {
  -webkit-font-smoothing: initial;
  -moz-font-smoothing: initial;
  font-smooth: initial;
}
#search .row,
#toggles .row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 0;
  max-width: 64rem;
  position: relative;
}
#search .row:after,
#search .row:before,
#toggles .row:after,
#toggles .row:before {
  content: " ";
  display: table;
}
#search .row:after,
#toggles .row:after {
  clear: both;
}
#search .row.pad,
#toggles .row.pad {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  width: 100%;
}
#toggles {
  position: fixed;
  text-align: right;
  top: 0;
  width: 100%;
  pointer-events: none;
  z-index: 11;
}
#toggles a.button {
  pointer-events: auto;
  background-color: #900;
  color: #fff;
  font-family: BentonSansBold, Arial, sans-serif;
  font-weight: 400;
  line-height: 34px;
  margin-top: 10px;
  margin-left: 5px;
  padding: 0 0.5rem;
  position: relative;
  text-transform: uppercase;
  text-decoration: none;
  width: auto;
}
#toggles .button {
  float: none;
}
#toggles a.button:hover {
  color: #fff;
}
.touch #toggles a.button.hover {
  background-color: #fff !important;
  color: #900 !important;
}
#toggles a.search-toggle {
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  line-height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #900;
  box-shadow: none;
  border: none;
  margin-top: 0.8rem;
  padding: 0;
  text-transform: none;
  float: right;
}
#toggles a.search-toggle:focus {
  box-shadow: none;
  outline: 0.125rem solid #990000;
  outline-offset: 0.125rem;
}
#toggles a.search-toggle img {
  width: 18px;
  height: 18px;
}
.search-open #toggles a.search-toggle img {
  width: 24px;
  height: 24px;
}
.images-off #toggles img,
.no-js #toggles img {
  width: auto !important;
  height: auto !important;
  line-height: 1;
}
#search {
  background-color: #900;
  height: 45px;
  padding-top: 6px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 8;
  -webkit-transition: height 0.42s ease-in-out;
  -moz-transition: height 0.42s ease-in-out;
  transition: height 0.42s ease-in-out;
}
.search-open #search {
  height: 50px;
}
#search[aria-hidden="true"] > .row {
  display: none;
}
#search[aria-hidden="false"] > .row {
  display: block;
}
#inline-search .gsc-branding,
#off-canvas-search .gsc-branding,
#search .gsc-branding {
  display: none;
}
#inline-search .gsc-search-button + .gsc-clear-button,
#off-canvas-search .gsc-search-button + .gsc-clear-button,
#search .gsc-search-button + .gsc-clear-button {
  display: none;
}
@media only screen and (min-width: 1025px) {
  #toggles {
    position: absolute;
    top: 5px;
  }
  #toggles a.button {
    display: none;
  }
  #toggles a.search-toggle {
    display: flex;
    float: right;
    margin: 0;
    width: 2.5rem;
    height: 2.5rem;
    top: 0.75rem;
    margin-top: 0;
  }
  #toggles a.search-toggle img {
    display: inline-block;
    vertical-align: middle;
    width: 18px;
    height: 18px;
  }
  #toggles a.search-toggle:focus,
  #toggles a.search-toggle:hover {
    color: #fff;
  }
  .search-open #toggles a.search-toggle img {
    width: 24px;
    height: 24px;
  }
  .search-open .is-sticky nav.main {
    -webkit-transform: translateY(50px);
    -moz-transform: translateY(50px);
    -ms-transform: translateY(50px);
    -o-transform: translateY(50px);
    transform: translateY(50px);
  }
  #search {
    position: absolute;
    top: 0;
  }
  .has-sticky #search {
    position: fixed;
    top: 0;
  }
  #search {
    height: 56px;
    transform: translateY(-56px);
    transition: transform 0.42s;
    overflow: hidden;
  }
  .search-open #search {
    height: 56px;
    transform: translateY(0);
    transition: transform 0.42s;
  }
  #toggles,
  div[id^="branding-bar"],
  section[id^="branding-bar"] {
    transition: margin-top 0.3s;
  }
  .search-open #toggles,
  .search-open div[id^="branding-bar"],
  .search-open section[id^="branding-bar"] {
    margin-top: 52px;
    transition: margin-top 0.5s;
  }
  .search-open.has-sticky div[id^="branding-bar"],
  .search-open.has-sticky section[id^="branding-bar"] {
    margin-top: 0;
  }
}
.search-box table {
