.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
#branding-bar,
#footer {
  -webkit-font-smoothing: initial;
  -moz-font-smoothing: initial;
  font-smooth: initial;
}
#branding-bar *,
#footer * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
#branding-bar .row,
#footer .row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 0;
  max-width: 64rem;
  position: relative;
}
#branding-bar .row:after,
#branding-bar .row:before,
#footer .row:after,
#footer .row:before {
  content: " ";
  display: table;
}
#branding-bar .row:after,
#footer .row:after {
  clear: both;
}
#branding-bar .row.pad,
#footer .row.pad {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  width: 100%;
}
#branding-bar {
  position: fixed;
  line-height: 1.5;
  top: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  margin-bottom: 0;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e2e7e9;
}
#branding-bar .show-on-mobile {
  display: inline;
}
#branding-bar .show-on-desktop,
#branding-bar .show-on-tablet {
  display: none;
}
#branding-bar:after {
  content: "";
  bottom: -3px;
  position: absolute;
  width: 100%;
  height: 3px;
  background: 0 0;
  z-index: 8;
}
#branding-bar img {
  top: 10px;
  width: 36px;
  position: absolute;
  left: 0.9375rem;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 9;
}
#branding-bar #iu-campus,
#branding-bar h1 {
  font-family: BentonSansBold, Arial, serif;
  position: relative;
  top: 0.55rem;
  padding-left: 48px;
  font-weight: 400;
  color: #243142;
  font-size: 18px;
  float: left;
  line-height: 45px;
  margin: 0;
  text-transform: uppercase;
  -webkit-transition: all 0.25s;
  -moz-transition: all 0.25s;
  transition: all 0.25s;
}
#branding-bar #iu-campus a,
#branding-bar h1 a {
  color: #243142;
  text-decoration: none;
}
#branding-bar #iu-campus span,
#branding-bar h1 span {
  font-family: BentonSansBold, sans-serif;
  text-transform: none;
}
#branding-bar #iu-campus .show-on-mobile,
#branding-bar #iu-campus .show-on-tablet,
#branding-bar h1 .show-on-mobile,
#branding-bar h1 .show-on-tablet {
  font-size: 17px;
}
#branding-bar a:focus {
  outline: 0;
}
#branding-bar a:focus span {
  outline: 0.125rem solid #990000;
  outline-offset: 0.125rem;
  padding: 2px;
}
#branding-bar.iupuc #iu-campus,
#branding-bar.iupui #iu-campus {
  font-size: 1.5rem;
}
#branding-bar.iupuc #iu-campus .show-on-mobile,
#branding-bar.iupuc #iu-campus .show-on-tablet,
#branding-bar.iupui #iu-campus .show-on-mobile,
#branding-bar.iupui #iu-campus .show-on-tablet {
  font-size: 1.5rem;
}
#footer {
  padding: 24px 0;
  text-align: center;
  position: relative;
  font-family: BentonSansRegular, Arial, serif;
  font-weight: 400;
}
#footer a {
  color: #900;
  text-decoration: none;
}
#footer a:hover {
  text-decoration: underline;
}
#footer a:focus {
  outline: 0.125rem solid #990000;
  outline-offset: 0.125rem;
}
#footer .signature {
  text-align: center;
}
#footer .signature-link {
  background-image: none;
  display: inline-block;
  height: 0;
  padding-top: 36px;
  overflow: hidden;
  width: 240px;
}
#footer .signature-link.signature-img {
  background-image: none;
  height: auto;
  width: 15rem;
  padding-top: 0;
}
#footer p {
  font-size: 0.75rem;
  line-height: 24px;
  margin: 0;
  clear: left;
}
#footer .tagline {
  font-family: BentonSansCondBold, Arial, serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  background: #7a1705;
  color: #fff;
  font-size: 1.375rem;
  line-height: 2.5rem;
  margin: -24px -0.9375rem 24px -0.9375rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
#footer .tagline span {
  text-transform: lowercase;
  font-family: GeorgiaProBoldItalic, Georgia, serif;
  font-size: 1rem;
  vertical-align: 2px;
  letter-spacing: 0;
  margin-left: -4px;
}
#footer .tagline a:focus {
  outline-color: #fff;
}
#footer .tagline.bicentennial {
  font-family: inherit;
  letter-spacing: 0.1em;
}
#footer .tagline.bicentennial.celebration {
  font-size: inherit;
  padding-bottom: 2px;
}
#footer .tagline.celebration a {
  color: #fff;
  text-decoration: none;
}
#footer .tagline.celebration svg {
  vertical-align: middle;
}
@media screen and (max-width: 350px) {
  #footer .line-break-small {
    display: block;
  }
}
@media screen and (max-width: 420px) {
  #footer .line-break {
    display: block;
  }
  #footer .hide-on-mobile {
    display: none;
  }
}
@media only screen and (min-width: 40em) {
  #branding-bar .show-on-tablet {
    display: inline;
  }
  #branding-bar .show-on-desktop,
  #branding-bar .show-on-mobile {
    display: none;
  }
  #footer {
    text-align: left;
    border-top: 6px solid maroon;
    padding: 32px 0 24px;
  }
  #footer p {
    line-height: 2.25rem;
  }
  #footer .signature {
    float: left;
    text-align: left;
    height: 36px;
  }
  #footer .copyright {
    float: right;
    clear: right;
    text-align: right;
  }
  #footer .tagline {
    font-size: 1.5rem;
    position: absolute;
    left: 50%;
    margin: 0 -200px 0 0;
    top: -55px;
    padding: 0 24px;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
  }
  #footer .tagline.bicentennial {
    padding: 0 16px;
  }
}
@media only screen and (min-width: 1025px) {
  #branding-bar {
    padding-bottom: 1.6rem;
    position: relative;
  }
  #branding-bar:after {
    height: 0;
  }
  #branding-bar img {
    position: absolute;
    width: 2.5rem;
    top: 1rem;
  }
  #branding-bar #iu-campus,
  #branding-bar h1 {
    line-height: inherit;
    top: 0;
    padding-top: 1.45rem;
    padding-left: 3.4rem;
    font-size: 1.25rem;
  }
  #branding-bar .search {
    margin-top: 6px;
    float: right;
    width: 250px;
  }
  #branding-bar input[type="text"] {
    width: 80%;
    float: left;
    margin-bottom: 0;
  }
  #branding-bar input[type="submit"].button {
    width: 20%;
    float: left;
    border-radius: 0;
  }
}
@media only screen and (min-width: 1025px) {
  #branding-bar .show-on-desktop {
    display: inline !important;
  }
  #branding-bar .show-on-desktop,
  #branding-bar .show-on-tablet {
    display: none;
  }
}
